import React, { Component } from 'react';
import styled from 'styled-components';
import Input from './Input';
import Button from './Button';

const Container = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 20px;
`;

const List = styled.li`
  display: flex;
  margin: 10px;
  text-decoration: none;
  list-style-type: none;
`;
const Fibo = styled.div`
  color: #8e44ad;
  width: 100%;
  height: 100%;
  font-size: 30px;
  font-weight: bold;
`;
const Tree = styled.span`
  transform: rotate(-48deg);
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 12px;
  margin-left: -51px;
  margin-top: 10px;
  margin-bottom: -72px;
`;
const Sum = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: white;
  width: 150px;
  height: 100%;
`;

export default class Fibonacci extends Component {
  constructor(props) {
    super(props);
    this.state = { value: '' };
  }

  fibonacci(num) {
    var fibo = [0, 1];

    if (num === 0) fibo = [0];

    for (var i = 2; i <= num; i++) {
      fibo[i] = fibo[i - 1] + fibo[i - 2];
    }

    return fibo;
  }
  handleChange(event) {
    console.log(this.state);
    this.setState({ value: event.target.value });
  }

  render() {
    return (
      <Container>
        <Input
          placeholder="Enter Number"
          type="number"
          value={this.state.value}
          onChange={this.handleChange.bind(this)}
        />
        {this.fibonacci(parseInt(this.state.value, 10)).map((num, index) => {
          return (
            <List>
              {num % 2 === 0 ? (
                <Fibo>
                  {num}
                  <Tree />
                </Fibo>
              ) : (
                <Sum>{num}</Sum>
              )}
            </List>
          );
        })}
      </Container>
    );
  }
}



