import React from 'react';
import styled from 'styled-components';

export default styled.input`
  display: flex;
  align-self: center;
  border-radius: 0.5;
  width: 300px;
  height: 50px;
  background-color: #0984e3;
  border: solid 4px #a29bfe;
  font-size: 25px;
  text-align: center;
`;



