import React from 'react';
import styled from 'styled-components';

export default styled.button`
  width: 300px;
  background-color: ${props => (props.primary ? '#a29bfe' : 'white')};
  border: ${props => (props.primary ? 'solid 4px #6c5ce7' : 'white')};
`;



